.infomenu {
  width: 100%;
  height: 100px;
  align-items: center;
  &__container {
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &_logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &_namecompany{
    margin: 0;
    font-size: 16px;
    &_name{
      margin: 0;
      padding: 0;
      font-size: 24px;
      font-weight: 600;
      line-height: 38px;
    }
    &_descr {
      margin: 0;
      padding: 0;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
    }
  }
  &_scrolled {
    position: fixed;
    top: 0;
    z-index: 600;
    background-color: #d9d9d9;
    animation-name: fadeInUp;
    animation-duration: 1s;
  }
}
