.infobar {
  width: 100%;
  height: 40px;
  background-color: #d9d9d9;

  @include tablet {
    width: 100%;
    height: 80px;
  }
  @include mobile {
    height: 40px;
  }

  &__container {
    display: flex;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    @include tablet {
      max-width: 868px;
      justify-content: start;
      flex-wrap: wrap;
      flex-direction: column;
    }
    @include small-tablet {
      max-width: 668px;
    }
  }

  &_title {
    margin: 0;
    text-align: center;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #000000;
    @include tablet {
      display: flex;
      justify-content: center;
      height: 40px;
      font-size: 24px;
    }
    @include small-tablet {
      font-size: 20px;
      width: 100%;
    }
  }

  &_contacts {
    margin: 0;
    display: flex;
    justify-content: space-between;
    width: 643px;
    align-items: center;
    text-align: center;
    .image {
      @include small-tablet {
        display: none;
      }
    }
    & a {
      text-decoration: none;
      color: #000;
    }
    & a:hover {
      color: #000;
    }
    &_address {
      margin: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      width: 295px;
      height: 100%;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      color: #000000;
      .simplebar-placeholder {
        display: none;
      }
      .image {
        margin-right: 10px;
      }
      @include small-tablet {
        width: 47%;
      }
    }

    &_email {
      display: flex;
      width: 165px;
      height: 100%;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      color: #000000;
      align-items: center;
      text-align: center;
      .image {
        margin-right: 10px;
      }
      @include small-tablet {
        width: 22%;
      }
    }

    &_tel {
      display: flex;
      width: 148px;
      height: 100%;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      color: #000000;
      align-items: center;
      text-align: center;
      .image {
        margin-right: 10px;
      }
      @include small-tablet {
        width: 22%;
      }
    }

    @include tablet {
      height: 40px;
      justify-content: space-between;
    }
    @include small-tablet {
      width: 100%;
    }
    @include mobile {
      display: none;
    }
  }
}
