#box4 {
  width: 100%;
  height: 540px;
  display: flex;
  flex-wrap: wrap;
}
.box4-header {
  display: flex;
  justify-content: center;
  font-size: 32px;
  margin-top: 24px;
  margin-bottom: 24px;
}
.box4-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.box4-container .box4-services {
  position: relative;
  display: flex;
  justify-content: center;
  width: 255px;
  height: 100%;
  max-width: 255px;
  max-height: 350px;
  margin-top: 25px;
  background-color: #000;
  border-radius: 25px;
}
.box4-container .box4-services:not(:last-child) {
  margin-right: 25px;
}
.box4-container .box4-services .box4-services-bg {
  position: absolute;
  border-radius: 25px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.6;
}
.box4-container .box4-services .box4-services-header {
  position: absolute;
  display: flex;
  top: 25%;
  color: #fff;
  font-size: 24px;
  text-align: center;
  justify-content: center;
}
.box4-container .box4-services .box4-services-btn {
  position: absolute;
  display: flex;
  top: 65%;
  color: #fff;
  justify-content: center;
  border: 1px #fff solid;
  margin-top: 30px;
  padding: 10px 20px;
  border-radius: 25px;
}

.box4-container .box4-services .box4-services-btn .box4-services-btn-header {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
}
.box4-container .box4-services .box4-services-btn .box4-services-btn-header:hover {
  color: #d9d9d9;
}
