.footer{
  background-color: #d9d9d9;
  padding-top: 20px;
  padding: 20px 0;
  color: #8d8d8e;
}

.footer a {
  color: #8d8d8e;
}

.footer-info {
  display: flex;
  justify-content: space-between;
}

.footer-text{
  display: flex;
  align-items: center;

}

.copyright{
  padding-right: 5px;
  font-size: 14px;
}
.company{
  padding-right: 16px;
  margin-right: 16px;
  font-size: 14px;
}
.footer-menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
}
.footer-menu a {
  text-decoration: none;
  font-size: 14px;
  display: inline-block;
  margin-right: 30px;
}
.footer-menu a:hover{
  color: #569921;
}
.footer-button {
  background-color: transparent;
  border-radius: 50px;
  border: 1px solid #8d8d8e;
  padding: 8px 22px
}
.footercontainer{
  width: 100%;
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
}
