#box5 {
  width: 100%;
  height: 760px;
  display: flex;
  background-color: #d9d9d9;
}
.box5-header {
  text-align: center;
  font-size: 32px;
  margin-top: 10px;
  margin-bottom: 20px;
}
.box5-container {
  display: flex;
  justify-content: center;
}
.box5-map-600 {
  display: flex;
  position: relative;
  justify-content: center;
  width: 100%;
  max-width: 600px;
  height: 100%;
  max-height: 600px;
  margin-top: 60px;
  margin-right: 150px;
}
.box5-map-300 {
  display: none;
}
.box5-info {
  width: 100%;
  height: 100%;
  max-width: 900px;
  max-height: 600px;
}
