.hero {
  position: relative;
  height: 115vh;
  align-items: center;
  text-align: center;
  margin: auto;
  padding: auto;
  background-repeat: no-repeat;
  background-size: cover;
  @include small-tablet {
    height: 120vh;
  }
  &_Header{
      position: relative;
      z-index: 1;
      color: #fff;
      font-size: 3.6rem;
      font-family: 'Roboto', sans-serif;
      font-weight: 600;
      padding-top: 15%;
      @include mobile {
        font-size: 2.4rem;
        margin-bottom: 60px;
      }
  }
  &_btn {
      position: relative;
      z-index: 1;
      margin-top: 50px;
      &_element {
          z-index: 1;
          width: 300px;
          font-size: 20px;
          color: #fff;
          padding: 15px;
          font-family: 'Roboto', sans-serif;
          border-radius: 20px;
          background-color: Transparent;
          background-repeat:no-repeat;
          border: #fff 2px solid;
          cursor:pointer;
          overflow: hidden;
          transition: all .2s ease-in-out;
          &:hover{
              color: rgba(#fff, .5);
              text-decoration: none;
          }
          &:not(:last-child){
              margin-right: 20px;
              @include small-tablet {
                margin-right: 0;
              }
          }
          @include small-tablet  {
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            margin-bottom: 20px;
          }
          @include mobile {
            max-width: 576px;
            &:not(:first-child){
              margin-top: 40px;
          }
          }
      }
      @include small-tablet  {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
      }
      @include mobile {
        display: inline-flex;
        justify-content: center;
        flex-direction: column;

        .Main_btn_element:not(:last-child) {
          margin-bottom: 20px;
        }
      }
  }
  &_overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#000, .5);
    }
}
