/* stylelint-disable */

@mixin for-desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin desktop {
  @media (max-width: (1200px)) {
    @content;
  }
}

@mixin desktop1100 {
  @media (max-width: (1100px)) {
    @content;
  }
}


@mixin big-tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (968px)) {
    @content;
  }
}


@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
