.burger {
  position: relative;
  display: none;
  border: 1px solid #000000;
  border-radius: 15px;
  width: 32px;
  height: 32px;
  margin-right: 24px;
  @include tablet {
    display: block;
  }
  &__line {
    position: absolute;
    left: 50%;
    border-radius: 100px;
    height: 3px;
    width: 24px;
    background-color: #000;
    transform:translateX(-50%) translateY(-50%);

    &:first-child {
      margin-top: -7px;
    }

    &:last-child {
      margin-top: 7px;
    }
  }
}

.burger-close {
  display: none;
  position: absolute;
  right: var(--container-offset);
  top: 30px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  width: 44px;
  height: 44px;
  background-image: url("../../img/svg/close.svg");
  background-position: center;
  background-size: 14px 12px;
  background-repeat: no-repeat;

  @include tablet {
    display: block;
  }
}
