.slider_section{
  width: 100%;
  height: 900px;
  margin: auto;
  max-width: 1170px;
  display: flex;
  @include desktop1100 {
    justify-content: center;
    height: 700px;
  }
}
.slider {
  position: relative;
  width: 100%;

  @include desktop1100 {
    width: 80%;
    height: 650px;
  }
}
.slider-1 .slider__wrapper {
  overflow: hidden;
  width: 90%;
  align-items: center;
  text-align: center;
  margin: auto;
  height: 850px;
  @include tablet {
    height: 800px;
  }
}

.slider-1 .slider__items {
  display: flex;
  transition: transform 0.5s ease;
  will-change: transform;
  width: 100%;
  height: 100%;
  align-items: center;
}


.slider-1 .slider__item {
  flex: 0 0 100%;
  max-width: 100%;
  user-select: none;
  will-change: transform;
  height: 800px;
  @include tablet {
    height: 700px;
  }
}

.slider-1 .slider__item_img {
  width: 60%;
  height: 100%;
  background-size: contain;
  border: #000 1px solid;
  @include tablet {
    width: 450px;
    height: 550px;
  }
}

.partners_slider_container {
  width: 100%;
  max-width: 1170px;
  margin: auto;
  height: 300px;
  display: flex;
  align-items: center;
  text-align: center;

  @include desktop1100 {
    justify-content: center;
  }
}

.slider-2 .slider__wrapper {
  overflow: hidden;
  width: 85%;
  display: flex;
  align-items: center;
  text-align: center;
  margin: auto;
  height: 450px;
  @include desktop1100 {
    width: 65%;
    height: 650px;
  }
}


.slider-2 .slider__items {
  display: flex;
  justify-content: center;
  transition: transform 0.5s ease;
  will-change: transform;
  align-items: center;
  width: 100%;
  margin: auto;
  height: 400px;
}

.slider-2 .slider__item_wrapper {
    border: #000 2px solid;
    border-radius: 40px;
    width: 300px;
    height: 100px;
    margin-left: 15px;
    margin-right: 15px;
}

.slider-2 .slider__item_wrapper_img {
  width: 70%;
  height: 100%;
  object-fit: contain;
}

/* кнопки влево и вправо */

.slider__control {
  position: absolute;
  top: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 50px;
  color: #fff;
  text-align: center;
  background: rgb(0 0 0 / 20%);
  border: none;
  transform: translateY(-50%);
  cursor: pointer;
}

.slider__control_hide {
  display: none;
}

.slider__control[data-slide="prev"] {
  left: 0px;
}

.slider__control[data-slide="next"] {
  right: 0;
}

.slider__control:hover,
.slider__control:focus {
  color: #fff;
  text-decoration: none;
  background: rgb(0 0 0 / 30%);
  outline: 0;
}

.slider__control::before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  background: transparent no-repeat center center;
  background-size: 100% 100%;
}

.slider__control[data-slide="prev"]::before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}

.slider__control[data-slide="next"]::before {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}

.slider__indicators {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  margin: 0 15%;
  padding-left: 0;
  padding-top: 20px;
  list-style: none;
}

.slider__indicators li {
  flex: 0 1 auto;
  box-sizing: content-box;
  width: 30px;
  height: 5px;
  margin-right: 3px;
  margin-left: 3px;
  text-indent: -999px;
  background-color: rgb(0 0 0 / 50%);
  background-clip: padding-box;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  cursor: pointer;
}

.slider__indicators li.active {
  background-color: rgb(0 0 0 / 90%);
}
