.nav {
  width:  487px;
  height: 20%;
  align-items: center;
  text-align: center;
  &__list {
    display: flex;
    justify-content: space-between;

    @include tablet {
      @include flex-all-center;

      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }

  &_item {
    @include tablet {
      margin-right: 0;
      margin-bottom: 30px;
    }
  }

  &_link {
    @include tablet {
      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
    }
  }

  @include tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: #d9d9d9;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}
.menu--active {
  opacity: 1;
  visibility: visible;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    top: -50px;
  }

  to {
    opacity: 1;
    top: 0;
  }
}
