.About {
  position: relative;
  width: 100%;
  height: 105vh;
  align-items: center;
  display: flex;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
  &-container {
      width: 100%;
      max-width: 1170px;
      margin: auto;
  }
  &-overlay {
      width: 100%;
      height: 100%;
      position: absolute;
      background-color: rgba($color: #000000, $alpha: 0.5);
  }
  &-Text {
      width: 100%;
      max-width: 1170px;
      position: absolute;
      color: #fff;
      z-index: 1;
      &-Header{
          font-size: 48px;
          margin-bottom: 20px;
          text-align: center;
      }
      &-Description {
          margin-top: 40px;
          font-weight: 500;
          font-size: 16px;
          line-height: 19px;
          @include desktop1100 {
            margin: 0;
            margin: auto;
            max-width: 900px;
          }
          @include tablet {
            margin: 0;
            margin: auto;
            max-width: 600px;
          }
      }
  }
  @include desktop1100 {
    height: 180vh;
  }
}
