.graph-modal__close {
  right: 20px;
  top: 20px;
  width: auto;
  height: auto;
  font-weight: 400;
  font-size: 18px;
  line-height: 160%;
  color: #000;
}

.graph-modal {
  @include mobile {
    width: 100%;
    height: 100%;
  }
}

.graph-modal__container {
  min-height: 450px;
  max-height: 550px;
  margin: auto;
  width: 500px;
  @include mobile {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100%;
    max-height: 550px;
    transform:translateY(40%);
  }
}

.popup {
  align-items: center;
  margin: auto;
  @include mobile {
    overflow-y:auto;
  }
  &-header {
    width: 100%;
    font-size: 24px;
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid #000;
  }

  &-body {
    width: 100%;
    align-items: center;
    margin: auto;
    text-align: center;
    padding-bottom: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid #000;
    &_accept_btn{
      width: 235px;
      height: 50px;
      margin: auto;
      margin-top: 25px;
      outline: none;
      border: none;
      border-radius: 25px;
      background-color: #D9D9D9;
      transition: all .3s ease-in-out;
      &:focus {
          outline: none;
          background-color: #D9D9D9;
      }
      &:hover {
          background-color: #d9d9d0;
          color: rgba(#000, 0.5);
      }
  }
    & input {
      width: 90%;
      height: 40px;
      padding: 5px;
      margin-bottom: 30px;
      border: none;
      border-bottom: rgba(#000, .5) 1px solid;
      opacity: 0.7;

      &:focus {
        outline: none;
        border-bottom: 2px solid #559E23;
      }
    }

    & textarea {
      width: 90%;
      min-height: 50px;
      max-height: 150px;
      padding: 5px;
      border: none;
      border-bottom: rgba(#000, .5) 1px solid;
      opacity: 0.7;
      resize: vertical;
      &:focus {
          outline: none;
      }

      &:focus {
          outline: none;
          border-bottom: 2px solid #559E23;
      }
      @include mobile {
        resize: none;
        max-height: 140px;
      }
    }
  }
  .popup-footer {
    display: inline-block;
    @include mobile {
      width: 100%;
      align-items: center;
    }
    .contacs {
        display: flex;
        justify-content: center;
        margin-bottom: 10px;
        .email {
            margin-right: 10px;
        }
    }
    & h5 {
        font-size: 12px;
        text-align: center;
    }
  }
}
.simplebar{
  max-height: 350px;
}
.modal-body-text {
  max-height: 350px;
  margin: 0;
}
.modal-body-text p {
  margin: 0;
  text-align: left;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
}
